.sunrise {
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__time {
    font-size: 28px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.07px;

    margin-top: 22px;
  }

  &__sunset-time {
    letter-spacing: 1px;
    text-transform: capitalize;
  }
}
